import React from 'react';

import { register } from './UserFunction';

class Register extends React.Component {
    constructor() {
        super()
        this.state = {
            username: '',
            last_name: '',
            email: '',
            password: '',
        }
    }

    onChange = (e) => {
        this.setState({
            [e.target.name]: e.target.value
        })
    }
    onSubmit = e => {
        e.preventDefault()

        const user = {
            username: this.state.username,
            last_name: this.state.last_name,
            email: this.state.email,
            password: this.state.password,
        }
        register(user).then(res=> {
                this.props.history.push('/login')
        })
    }

    render() {
        return(
            <div className="container">
                <div className="row">
                    <div className="col-md-6 mt-t mx-auto">
                        <form noValidate onSubmit={this.onSubmit}>
                            <h1 className="h3 mb-3">Prosze sie zarejestrowac!</h1>

                            <div className="form-group">
                            <label htmlFor="username">username</label>
                            <input type="text" className="form-control" name="username" placeholder="enter username" value={this.state.username} onChange={this.onChange}>
                            </input>
                            </div>
                           
                            <div className="form-group">
                            <label htmlFor="lsst_name">Last name</label>
                            <input type="text" className="form-control" name="last_name" placeholder="enter last_name" value={this.state.last_name} onChange={this.onChange}>
                            </input>
                            </div>

                            <div className="form-group">
                            <label htmlFor="email">Email adress</label>
                            <input type="email" className="form-control" name="email" placeholder="enter email" value={this.state.email} onChange={this.onChange}>
                            </input>
                            </div>
                            
                            <div className="form-group">
                            <label htmlFor="password">password</label>
                            <input type="password" className="form-control" name="password" placeholder="enter email" value={this.state.password} onChange={this.onChange}>
                            </input>
                            </div>

                            <button type="submit" className="btn btn-lg">
                                Zarejestruj sie
                            </button>
                        </form>
                    </div>
                </div>
            </div>
        )       
    }
}


export default Register;